//--------------------------IMPORTS----------------------------
@import url("https://fonts.googleapis.com/css2?family=Red+Hat+Text:wght@700&display=swap");

//--------------------------COLORS----------------------------

// Primary
$grayish-blue: hsl(237, 18%, 59%);
$soft-red: hsl(345, 95%, 68%);

// Neutral
$white: hsl(0, 0%, 100%);
$dark-desaturated-blue: hsl(236, 21%, 26%);
$very-dark-blue: hsl(235, 16%, 14%);
$very-dark-mostly-black-blue: hsl(234, 17%, 12%);
$top-card: #2c2c44;
$bottom-card: #34364f;
//--------------------------STYLES----------------------------

* {
  padding: 0;
  margin: 0;
}

html {
  height: 100vh;
  font-family: "Red Hat Text", sans-serif;
}

body {
  height: 100%;
  background: url("../images/bg-stars.svg") top/100% 80% no-repeat,
    url("../images/pattern-hills.svg") bottom/100% 20% no-repeat,
    linear-gradient(to bottom, $very-dark-mostly-black-blue, $very-dark-blue);
  background-repeat: no-repeat;

  .container {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;

    .title {
      margin: 8.5rem 8rem 8rem 8rem;
      font-weight: 700;
      color: $white;
      text-transform: uppercase;
      letter-spacing: 0.8rem;
    }

    .countdown-clock {
      display: flex;
      flex-direction: row;
      justify-content: space-evenly;
      width: 60rem;

      .countdown {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;

        .countdown-card {
          display: flex;
          flex-direction: column;
          align-items: center;
          width: 12rem;
          border: 1px solid red;

          .card-top {
            height: 6rem;
            width: 100%;
            border: 1px solid blue;
            background-color: $top-card;
            transform-origin: bottom;
            overflow: hidden;
            position: relative;

            span {
              position: absolute;
              bottom: 0;
              left: 50%;
              transform: translate(-50%, 50%);
              font-size: 7rem;
              line-height: 1;
              color: $soft-red;
            }
          }

          .card-bottom {
            height: 6rem;
            width: 100%;
            border: 1px solid blue;
            background-color: $bottom-card;
            transform-origin: top;
            overflow: hidden;
            position: relative;

            span {
              position: absolute;
              top: 0;
              left: 50%;
              transform: translate(-50%, -50%);
              font-size: 7rem;
              line-height: 1;
              color: $soft-red;
            }
          }
        }

        .flip-card {
          @extend .countdown-card;
          animation: transform 1s;
          perspective: 800px;
          top: 0;
          position: absolute;
          z-index: 1;

          .animate-top {
            animation: top-to-bottom 0.5s ease-in;
          }

          @keyframes top-to-bottom {
            0% {
              transform: rotate3d(0);
            }
            100% {
              transform: rotate3d(-1, 0, 0, 90deg);
            }
          }

          .animate-bottom {
            animation: bottom-to-top 1s ease-out;
          }

          @keyframes bottom-to-top {
            0% {
              transform: rotate3d(1, 0, 0, 90deg);
            }
            50% {
              transform: rotate3d(1, 0, 0, 90deg);
            }
            100% {
              transform: rotate3d(0);
            }
          }
        }
        p {
          margin: 2rem 0;
          text-transform: uppercase;
          letter-spacing: 0.6rem;
          color: $grayish-blue;
        }
      }
    }

    footer {
      position: absolute;
      bottom: 0;

      .social {
        display: flex;
        justify-content: space-evenly;
        padding: 2rem;
      }

      .attribution {
        padding: 2rem;
        font-size: 11px;
        text-align: center;
        p {
          color: $grayish-blue;
          a {
            color: hsl(228, 45%, 44%);
          }
        }
      }
    }
  }
}
